*, :after, :before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  padding: 0 15px;
}

html {
  background: url("../images/background.jpg");
}

body {
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.5);
}